/**
 * Speech therapist dashboard - select target phonetic sounds for users
 * Stores in MongoDB (MONGODB_URI)
 */
'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import Image from 'next/image';
import { PHONETIC_SOUNDS } from '../../lib/models/UserProfile';

export default function TherapistPage() {
  const [targetSounds, setTargetSounds] = useState<string[]>([]);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);

  useEffect(() => {
    fetch('/api/therapist?userId=default')
      .then((r) => r.json())
      .then((d) => {
        setTargetSounds(d.targetSounds || []);
      })
      .finally(() => setLoading(false));
  }, []);

  const toggle = (sound: string) => {
    const next = targetSounds.includes(sound)
      ? targetSounds.filter((s) => s !== sound)
      : [...targetSounds, sound];
    setTargetSounds(next);
  };

  const save = async () => {
    setSaving(true);
    try {
      const res = await fetch('/api/therapist', {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          userId: 'default',
          targetSounds,
        }),
      });
      if (!res.ok) throw new Error('Save failed');
    } catch (e) {
      console.error(e);
      alert('Failed to save. Is MONGODB_URI set?');
    } finally {
      setSaving(false);
    }
  };

  return (
    <div className="app-container">
      <div className="flex-1 flex flex-col p-6">
        <div className="content-wrapper max-w-2xl mx-auto w-full">
          <div className="flex items-center justify-between mb-8">
            <Link
              href="/"
              className="text-white/60 hover:text-white transition-colors"
            >
              ‚Üê Back to Home
            </Link>
            <Image
              src="/phoneticallylogo.png"
              alt="Phonetically"
              width={40}
              height={40}
            />
          </div>

          <h1 className="text-3xl font-bold text-white mb-2">
            Therapist Dashboard
          </h1>
          <p className="text-white/60 mb-8">
            Select target sounds for the user&apos;s practice plan. Stored in
            MongoDB.
          </p>

          {loading ? (
            <p className="text-white/70">Loading...</p>
          ) : (
            <>
              <div className="glass-panel p-6 mb-6">
                <h2 className="text-lg font-semibold text-white mb-4">
                  Target sounds
                </h2>
                <div className="flex flex-wrap gap-2">
                  {PHONETIC_SOUNDS.map((sound) => (
                    <button
                      key={sound}
                      type="button"
                      onClick={() => toggle(sound)}
                      className={`sound-chip ${
                        targetSounds.includes(sound) ? 'sound-chip-selected' : ''
                      }`}
                    >
                      {sound}
                    </button>
                  ))}
                </div>
                <p className="text-white/50 text-sm mt-4">
                  Selected: {targetSounds.length}
                </p>
              </div>

              <button
                onClick={save}
                disabled={saving}
                className="glass-button px-6 py-3 font-semibold"
              >
                {saving ? 'Saving...' : 'Save to DB'}
              </button>
            </>
          )}
        </div>
      </div>
    </div>
  );
}
